import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { CashEqEcommerceComponent } from "../cash-eq-ecommerce/cash-eq-ecommerce.component";
import { CashEqGeneralComponent } from "../cash-eq-general/cash-eq-general.component";
import { CashEqComponent } from "../cash-eq/cash-eq.component";
import { PricePackagesECommerceComponent } from "../price-packages/package-ecommerce/package-ecommerce.component";
import { PricePackagesGeneralComponent } from "../price-packages/package-general/package-general.component";
import { PricePackagesTableComponent } from "../price-packages/packages-table/packages-table.component";
import { RetailAdminItemsAndPricingComponent } from "./items-and-pricing.component";

const routes: Routes = [
  {
    path: '',
    component: RetailAdminItemsAndPricingComponent
  },
  {
    path: 'packages',
    component: PricePackagesTableComponent
  },
  {
    path: 'packages/:packageId/general',
    component: PricePackagesGeneralComponent
  },
  {
    path: 'packages/:packageId/ecommerce',
    component: PricePackagesECommerceComponent
  },
  {
    path: 'cash-eqs',
    component: CashEqComponent
  },
  {
    path: 'cash-eqs/:back-office-id/:item-code/general',
    component: CashEqGeneralComponent
  },
  {
    path: 'cash-eqs/:back-office-id/:item-code/ecommerce',
    component: CashEqEcommerceComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RetailAdminItemsAndPricingRoutingModule { }